    ul {
      margin: 0;
      padding: 0 0 10px;
      text-align: center;

      li {
        display: inline-block;
        margin: 0 5px 10px;
        a {
          color: #fff;
          text-align: center;
          line-height: 34px;
          display: block;
          font-size: 16px;
          width: 35px;
          height: 35px;
          border: 1px solid rgba(255,255,255,.3);
          -webkit-border-radius: 50%;
          -moz-border-radius: 50%;
           border-radius: 50%;
        }
        a:hover {
          background-color: #fff;
          color: #000;
        }
      }
    }

    p {
      font-size: 16px;
    color: #8c8c8c;
    }
  }

  #button {
    display: inline-block;
    background-color: #3649a8;
    width: 45px;
    height: 45px;
    text-align: center;
    border-radius: 2px;
    position: fixed;
    bottom: 30px;
    right: 30px;
    transition: background-color .3s, 
    opacity .5s, visibility .5s;
    opacity: 0;
    visibility: hidden;
    z-index: 1000;
  }
  
  #button::after {
    content: "\f077";
    font-family: FontAwesome;
    font-weight: normal;
    font-style: normal;
    font-size: 1.7em;
    padding-bottom: 10px;
    line-height: 40px;
    color: #fff;
  }
  #button:hover {
    cursor: pointer;
    background-color: #1a62ce;
  }
  #button:active {
    background-color: #555;
  }
  #button.show {
    opacity: 1;
    visibility: visible;
  }
}

