.navigation {
  position: absolute;
  z-index: 9999;
  right: 200px;

  .navbar-light {
    .navbar-nav {
      .nav-link {
        color: #000;
        font-size: 20px;
        margin: 10px;
        text-transform: uppercase;
        font-weight: 600;
        text-shadow: 1px 1px 1px rgba(0,0,0,0.7);


        &:after {
          display:block;
          content: '';
          border-bottom: solid 3px #949292;  
          transform: scaleX(0);  
          transition: transform 250ms ease-in-out;
        }

        &:hover {
          color: #949292;

        }

        &:hover:after {
          transform: scaleX(1);
        }
      }
    }
  }  

}

